    <account-error-list v-if="authError"></account-error-list>
    <div class="background_login">
        <div class="shape_login"></div>
        <div class="shape_login"></div>
    </div>
    <form class="form_login" @submit.prevent="login(credentials)">
        <h3>Login Here</h3>

        <label class="label_login" for="username">Username</label>
        <input class="input_login" v-model="credentials.username" type="text" id="username" placeholder="Username" required />

        <label class="label_login" for="password">Password</label>
        <input class="input_login" v-model="credentials.password" type="password" id="password" placeholder="Password" required />

        <button class="button_login">Log In</button>
    </form>
  </div>
</template>

<script>
  import { mapActions, mapGetters } from 'vuex'
  import AccountErrorList from '@/components/accounts/AccountErrorList.vue'

  export default {
    name: 'LoginView',
    components: {
      AccountErrorList,
    },
    data() {
      return {
        credentials: {
          username: '',
          password: '',
        }
      }
    },
  computed: {
      ...mapGetters(['authError'])
    },
    methods: {
      ...mapActions(['login'])
    },
  }
</script>

